
.apply .hint {
  margin-top: 2.666667rem;
  color: #666666;
  text-align: center;
  font-size: 0.533333rem;
}
.apply .van-icon-clear {
  font-size: 0.48rem;
}
.apply .van-search {
  padding: 0.266667rem 0.48rem;
}
.apply .van-cell {
  line-height: 0.96rem;
}
.apply .van-field__control {
  font-size: 0.426667rem;
  text-align: center;
}
.apply .van-search .van-cell {
  border-radius: 0.133333rem;
}
.apply .van-field__left-icon .van-icon,
.apply .van-field__right-icon .van-icon {
  font-size: 0.533333rem;
}
.apply .apply-list {
  padding: 0 0.48rem;
  position: relative;
  background: #fff;
}
.apply .apply-join {
  position: absolute;
  right: 0.506667rem;
  top: 0.506667rem;
  width: 1.626667rem;
  height: 0.64rem;
  border: 1PX solid #E62129;
  border-radius: 0.066667rem;
  font-size: 0.373333rem;
