  height: 100%;
}
.apply .apply-name {
  margin-top: 0.466667rem;
}
.apply .apply-name p {
  width: 5.333333rem;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.apply .apply-ye {
  height: 0.346667rem;
  background: #F0DFB8;
  border-radius: 0.04rem;
  font-size: 0.24rem;
  color: #fff;
  margin-right: 0.2rem;
  text-align: center;
  padding: 0.066667rem;
  padding-right: 0.106667rem;
}
.apply .apply-ye img {
  width: 0.253333rem;
  height: 0.213333rem;
  margin-right: 0.066667rem;
}
.apply .ddd {
  background: #CEDDF1;
}


