
.friends {
  -webkit-overflow-scrolling: touch;
  position: relative;
  background: #fff;
}
.friends .f-person {
  width: 100%;
  height: 5.453333rem;
  background: url(../assets/img/bg.png) no-repeat;
  background-size: cover;
  padding-top: 1.253333rem;
  box-sizing: border-box;
  position: relative;
}
.friends .f-person .van-icon {
  position: absolute;
  left: 0.52rem;
  top: 0.426667rem;
  color: #fff;
  font-size: 0.4rem;
  font-weight: bold;
}
.friends .f-person .p-img {
  height: 1.72rem;
  width: 1.72rem;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
}
.friends .f-person .p-img img {
  width: 100%;
}
.friends .f-person .p-name {
  margin: 0 auto;
  text-align: center;
  width: 5.6rem;
}
.friends .f-person .p-name .name {
  color: #fff;
  font-size: 0.48rem;
  margin: 0.16rem 0 0.32rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.friends .f-person .p-name .company {
  color: #fff;
  font-size: 0.373333rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.friends .f-person .p-name .start {
  color: #DDD;
  font-size: 0.266667rem;
  margin-top: 0.053333rem;
  text-align: left;
}
.friends .f-person .p-name .s-top {
  margin-top: 0.133333rem;
}
.friends .f-person .p-name .p-ye {
  color: #FFEE31;
}


