
@import '../assets/iconfont.css';
.notice {
  background: #F5F5F5;
  height: auto;
  margin: 0 auto;
}
.notice .hei {
  margin-top: 0.213333rem;
}
.notice .notice-h {
  height: 1.6rem;
  padding: 0 0.48rem;
  font-size: 0.48rem;
  background: #fff;
  border-bottom: 1PX solid #f5f5f5;
  position: relative;
  color: #000;
}
.notice .notice-h .notice-img {
  height: 1.12rem;
  width: 1.12rem;
  border-radius: 50%;
  overflow: hidden;
  margin-top: 0.24rem;
  margin-right: 0.32rem;
}
.notice .notice-h .notice-img img {
  height: 100%;
}
.notice .notice-h .notice-t {
  margin-top: 0.266667rem;
}
.notice .notice-h .notice-t p {
  width: 4.666667rem;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.notice .notice-h .notice-s {
  font-size: 0.373333rem;
  color: #999;
  margin-top: 0.066667rem;
}
.notice .notice-h .notice-btn {
  position: absolute;
  right: 0.48rem;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  background: #f8f8f8;
  font-size: 0.373333rem;
  width: 1.626667rem;
  height: 0.64rem;
  line-height: 0.64rem;
  color: #E62129;
  border-radius: 0.066667rem;
  border: 1PX solid #f8f8f8;
}
.notice .notice-h .apply {
  background: #fff;
  color: #E62129;
  border-color: #E62129;
}
.notice .notice-h .agree {
  background: #E62129;
  border-color: #E62129;
  color: #fff;
}
.notice .notice-h .invalid {
  background: #999;
  border-color: #999;
  color: #fff;
}


