
.create {
  background: #f5f5f5;
  position: relative;
  height: 100%;
}
.create .toast {
  position: fixed;
  left: 50%;
  top: 50%;
  background: #000;
  padding: 0.266667rem;
  font-size: 0.346667rem;
  border-radius: 0.133333rem;
  transform: translateX(-50%);
  color: #fff;
}
.create-put {
  margin-top: 0.213333rem;
}
.van-cell {
  padding: 10 0.48rem;
  line-height: 1.28rem;
  font-size: 0.426667rem;
  font-weight: 500;
}
.van-field .van-cell__title {
  max-width: 2rem;
}
.create-btn {
  height: 1.28rem;
  width: 9.04rem;
  border-radius: 0.16rem;
  margin: 0;
  padding: 0;
  border: 1PX solid transparent;
  outline: none;
  background: #E62129;
}
.c-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 2.8rem;
  font-size: 0.48rem;
  color: #fff;
}
.l-put {
  height: 1.28rem;
  font-size: 0.48rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  background: #fff;
  -ms-flex-align: center;
      align-items: center;
}
.l-put .label {
  width: 3.333333rem;
  padding-left: 0.48rem;
  color: #000;
}
.l-put input {
  width: 100%;
  text-align: left;
  background: none;
  outline: none;
  border: 0;
  caret-color: blue;
  padding: 0.266667rem 0;
  padding-right: 0.266667rem;
}


