
@import "../assets/icon/style.css";
.bar a:hover {
  text-decoration: none;
}
.bar .nav {
  height: calc(100vh - 1.333333rem);
  overflow: auto;
}
.bar .bar-b {
  background: #fff;
  height: 1.333333rem;
  width: 100%;
  position: fixed;
  z-index: 99;
  left: 0;
  right: 0;
  bottom: 0;
}
.bar .bar-b ul {
  display: -ms-flexbox;
  display: flex;
}
.bar .bar-b ul li {
  width: 20%;
  line-height: 0.666667rem;
  text-align: center;
  padding-top: 0.266667rem;
  padding-bottom: 0.133333rem;
  box-sizing: border-box;
  color: #999;
}
.bar .bar-b ul li span {
  background-size: cover;
  display: inline-block;
  height: 0.533333rem;
}
.bar .active .icon-dkq {
  width: 0.653333rem;
  background: url("../assets/img/dkq1.png");
}
.bar .active .icon-jl {
  width: 0.52rem;
  background: url("../assets/img/jl1.png");
}
.bar .active .icon-dk {
  width: 0.586667rem;
  background: url("../assets/img/dk1.png");
}
.bar .active .icon-qz {
  width: 0.626667rem;
  background: url("../assets/img/qz1.png");
}
.bar .active .icon-sz {
  width: 0.533333rem;
  background: url("../assets/img/sz1.png");
}
.bar .active .bar-s {
  color: #E62129;
}
.bar .active .icon {
  background-size: auto 100%;
  background-repeat: no-repeat;
  display: inline-block;
  height: 0.533333rem;
}
.bar .bar-s {
  color: #999;
  font-size: 0.266667rem;
}
.bar .icon-dkq {
  width: 0.653333rem;
  background: url("../assets/img/dkq.png");
}
.bar .icon-jl {
  width: 0.52rem;
  background: url("../assets/img/jl.png");
}
.bar .icon-dk {
  width: 0.586667rem;
  background: url("../assets/img/dk.png");
}
.bar .icon-qz {
  width: 0.626667rem;
  background: url("../assets/img/qz.png");
}
.bar .icon-sz {
  width: 0.533333rem;
  background: url("../assets/img/sz.png");
}


